"use client";

import { useSonos } from "@/components/sonos-provider";
import { Button } from "@/components/ui/button";
import { Slider } from "@/components/ui/slider";
import { Play, Pause, SkipBack, SkipForward, Volume2, Maximize2 } from "lucide-react";
import { useState } from "react";
import { FullScreenPlayer } from "./full-screen-player";

export function MiniPlayer() {
  const { currentGroup, playbackState, refreshState } = useSonos();
  const [isExpanded, setIsExpanded] = useState(false);

  if (!currentGroup) return null;

  const metadata = playbackState?.currentItem?.track || playbackState?.container || playbackState?.currentItem;
  const isPlaying = playbackState?.playbackState === "PLAYBACK_STATE_PLAYING";
  const artwork = metadata?.imageUrl;
  const title = metadata?.name || "No Music Playing";
  const artist = metadata?.description || "Select something to play";

  const togglePlayback = async () => {
    const command = isPlaying ? "pause" : "play";
    await fetch(`/api/sonos/control?groupId=${currentGroup.id}&command=${command}`, { method: "POST" });
    refreshState();
  };

  const skip = async (direction: "skipToNextTrack" | "skipToPreviousTrack") => {
    await fetch(`/api/sonos/control?groupId=${currentGroup.id}&command=${direction}`, { method: "POST" });
    refreshState();
  };

  return (
    <>
      <div className="fixed bottom-0 left-0 right-0 bg-background/80 backdrop-blur-xl border-t p-4 h-24 z-40">
        <div className="max-w-screen-2xl mx-auto flex items-center justify-between h-full gap-4">
          {/* Info */}
          <div className="flex items-center gap-4 w-1/3">
            <div className="h-16 w-16 bg-muted rounded-md overflow-hidden flex-shrink-0 shadow-lg border">
              {artwork && <img src={artwork} alt={title} className="w-full h-full object-cover" />}
            </div>
            <div className="flex flex-col truncate">
              <span className="font-semibold text-sm truncate">{title}</span>
              <span className="text-xs text-muted-foreground truncate">{artist}</span>
            </div>
          </div>

          {/* Controls */}
          <div className="flex flex-col items-center gap-2 w-1/3">
            <div className="flex items-center gap-6">
              <Button variant="ghost" size="icon" onClick={() => skip("skipToPreviousTrack")}>
                <SkipBack className="h-5 w-5 fill-current" />
              </Button>
              <Button 
                variant="secondary" 
                size="icon" 
                className="h-10 w-10 rounded-full" 
                onClick={togglePlayback}
              >
                {isPlaying ? <Pause className="h-5 w-5 fill-current" /> : <Play className="h-5 w-5 fill-current" />}
              </Button>
              <Button variant="ghost" size="icon" onClick={() => skip("skipToNextTrack")}>
                <SkipForward className="h-5 w-5 fill-current" />
              </Button>
            </div>
          </div>

          {/* Actions */}
          <div className="flex items-center justify-end gap-6 w-1/3">
            <div className="flex items-center gap-2 w-32 hidden lg:flex">
              <Volume2 className="h-4 w-4 text-muted-foreground" />
              <Slider defaultValue={[50]} max={100} step={1} className="w-full" />
            </div>
            <Button variant="ghost" size="icon" onClick={() => setIsExpanded(true)}>
              <Maximize2 className="h-4 w-4" />
            </Button>
          </div>
        </div>
      </div>

      {isExpanded && <FullScreenPlayer onClose={() => setIsExpanded(false)} />}
    </>
  );
}
