"use client";

import { useSonos } from "@/components/sonos-provider";
import { Button } from "@/components/ui/button";
import { Slider } from "@/components/ui/slider";
import { ChevronDown, Play, Pause, SkipBack, SkipForward, Volume2, Repeat, Shuffle } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";

export function FullScreenPlayer({ onClose }: { onClose: () => void }) {
  const { currentGroup, playbackState, refreshState } = useSonos();

  const metadata = playbackState?.currentItem?.track || playbackState?.container || playbackState?.currentItem;
  const isPlaying = playbackState?.playbackState === "PLAYBACK_STATE_PLAYING";
  const artwork = metadata?.imageUrl;
  const title = metadata?.name || "No Music Playing";
  const artist = metadata?.description || "Select something to play";

  const togglePlayback = async () => {
    const command = isPlaying ? "pause" : "play";
    await fetch(`/api/sonos/control?groupId=${currentGroup.id}&command=${command}`, { method: "POST" });
    refreshState();
  };

  const skip = async (direction: "skipToNextTrack" | "skipToPreviousTrack") => {
    await fetch(`/api/sonos/control?groupId=${currentGroup.id}&command=${direction}`, { method: "POST" });
    refreshState();
  };

  return (
    <motion.div
      initial={{ y: "100%" }}
      animate={{ y: 0 }}
      exit={{ y: "100%" }}
      transition={{ type: "spring", damping: 25, stiffness: 200 }}
      className="fixed inset-0 z-50 bg-background flex flex-col items-center justify-center p-8"
    >
      {/* Dynamic Background with Blur */}
      <div className="absolute inset-0 z-[-1] overflow-hidden">
        {artwork && (
          <img 
            src={artwork} 
            alt="Background" 
            className="w-full h-full object-cover opacity-30 scale-110 blur-3xl"
          />
        )}
        <div className="absolute inset-0 bg-gradient-to-t from-background via-transparent to-transparent" />
      </div>

      <header className="absolute top-0 left-0 right-0 p-8 flex items-center justify-between">
        <Button variant="ghost" size="icon" onClick={onClose}>
          <ChevronDown className="h-8 w-8" />
        </Button>
        <div className="text-center">
          <p className="text-xs font-bold uppercase tracking-widest text-muted-foreground">Playing from</p>
          <p className="text-sm font-semibold">{currentGroup?.name}</p>
        </div>
        <div className="w-10" /> {/* Spacer */}
      </header>

      <main className="max-w-4xl w-full grid grid-cols-1 md:grid-cols-2 gap-12 items-center">
        {/* Artwork */}
        <motion.div 
          layoutId="artwork"
          className="aspect-square w-full rounded-2xl overflow-hidden shadow-2xl border bg-muted"
        >
          {artwork && <img src={artwork} alt={title} className="w-full h-full object-cover" />}
        </motion.div>

        {/* Info & Controls */}
        <div className="flex flex-col space-y-12">
          <div className="space-y-2">
            <h1 className="text-4xl md:text-5xl font-bold tracking-tight">{title}</h1>
            <p className="text-xl md:text-2xl text-muted-foreground">{artist}</p>
          </div>

          <div className="space-y-8">
            <div className="space-y-2">
              <Slider defaultValue={[0]} max={100} step={0.1} className="w-full h-2" />
              <div className="flex justify-between text-xs font-medium text-muted-foreground">
                <span>0:00</span>
                <span>3:45</span>
              </div>
            </div>

            <div className="flex items-center justify-center gap-12">
              <Button variant="ghost" size="icon" onClick={() => skip("skipToPreviousTrack")}>
                <SkipBack className="h-10 w-10 fill-current" />
              </Button>
              <Button 
                variant="primary" 
                size="icon" 
                className="h-20 w-20 rounded-full shadow-2xl" 
                onClick={togglePlayback}
              >
                {isPlaying ? <Pause className="h-10 w-10 fill-current" /> : <Play className="h-10 w-10 fill-current ml-1" />}
              </Button>
              <Button variant="ghost" size="icon" onClick={() => skip("skipToNextTrack")}>
                <SkipForward className="h-10 w-10 fill-current" />
              </Button>
            </div>

            <div className="flex items-center justify-between pt-8">
              <Button variant="ghost" size="icon" className="text-muted-foreground">
                <Shuffle className="h-5 w-5" />
              </Button>
              <div className="flex items-center gap-4 flex-1 max-w-[200px] mx-8">
                <Volume2 className="h-5 w-5 text-muted-foreground" />
                <Slider defaultValue={[50]} max={100} step={1} className="flex-1" />
              </div>
              <Button variant="ghost" size="icon" className="text-muted-foreground">
                <Repeat className="h-5 w-5" />
              </Button>
            </div>
          </div>
        </div>
      </main>
    </motion.div>
  );
}
