"use client";

import { useSonos } from "@/components/sonos-provider";
import { Button } from "@/components/ui/button";
import { Speaker, Music2, Radio, Podcast, Heart, LayoutGrid } from "lucide-react";
import { cn } from "@/lib/utils";

const MENU_ITEMS = [
  { icon: LayoutGrid, label: "Dashboard", active: true },
  { icon: Music2, label: "My Music" },
  { icon: Heart, label: "Favorites" },
  { icon: Podcast, label: "Podcasts" },
  { icon: Radio, label: "Radio" },
];

export function ZoneSelector() {
  const { groups, currentGroup, setCurrentGroup } = useSonos();

  return (
    <div className="flex flex-col h-full py-6 space-y-8">
      <div className="px-4">
        <h2 className="text-xs font-bold uppercase tracking-[0.2em] text-muted-foreground mb-6 opacity-50 px-2">
          Menu
        </h2>
        <div className="space-y-1">
          {MENU_ITEMS.map((item) => (
            <Button
              key={item.label}
              variant="ghost"
              className={cn(
                "w-full justify-start gap-3 h-11 px-3 rounded-xl transition-all duration-200",
                item.active ? "bg-white/10 text-white shadow-sm" : "text-muted-foreground hover:bg-white/5 hover:text-white"
              )}
            >
              <item.icon className={cn("h-5 w-5", item.active ? "text-white" : "text-muted-foreground")} />
              <span className="font-medium">{item.label}</span>
            </Button>
          ))}
        </div>
      </div>

      <div className="px-4">
        <h2 className="text-xs font-bold uppercase tracking-[0.2em] text-muted-foreground mb-6 opacity-50 px-2">
          Rooms
        </h2>
        <div className="space-y-1">
          {groups.map((group) => (
            <Button
              key={group.id}
              variant="ghost"
              className={cn(
                "w-full justify-start gap-3 h-11 px-3 rounded-xl transition-all duration-200 group",
                currentGroup?.id === group.id 
                  ? "bg-white/10 text-white shadow-sm" 
                  : "text-muted-foreground hover:bg-white/5 hover:text-white"
              )}
              onClick={() => setCurrentGroup(group)}
            >
              <div className={cn(
                "h-2 w-2 rounded-full transition-all",
                currentGroup?.id === group.id ? "bg-green-500 shadow-[0_0_8px_rgba(34,197,94,0.6)]" : "bg-transparent group-hover:bg-white/20"
              )} />
              <Speaker className={cn("h-5 w-5", currentGroup?.id === group.id ? "text-white" : "text-muted-foreground")} />
              <span className="font-medium truncate">{group.name}</span>
            </Button>
          ))}
        </div>
      </div>
    </div>
  );
}
