"use client";

import { useSonos } from "@/components/sonos-provider";
import { Card, CardContent } from "@/components/ui/card";
import { Play } from "lucide-react";

export function FavoriteGrid() {
  const { favorites, currentGroup, refreshState } = useSonos();

  const playFavorite = async (favoriteId: string) => {
    if (!currentGroup) return;
    await fetch(`/api/sonos/control?groupId=${currentGroup.id}&favoriteId=${favoriteId}`, { method: "POST" });
    refreshState();
  };

  if (!favorites || favorites.length === 0) {
    return (
      <div className="flex flex-col items-center justify-center h-full text-center space-y-4">
        <p className="text-muted-foreground">No favorites found on your Sonos account.</p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <h2 className="text-xl font-semibold">Your Favorites</h2>
      <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-6">
        {favorites.map((favorite: any) => (
          <Card 
            key={favorite.id} 
            onClick={() => playFavorite(favorite.id)}
            className="group relative overflow-hidden border-none bg-secondary/50 hover:bg-secondary transition-colors cursor-pointer"
          >
            <CardContent className="p-0 aspect-square relative">
              {favorite.imageUrl ? (
                <img
                  src={favorite.imageUrl}
                  alt={favorite.name}
                  className="object-cover w-full h-full transition-transform duration-300 group-hover:scale-105"
                />
              ) : (
                <div className="w-full h-full bg-muted flex items-center justify-center">
                  <span className="text-muted-foreground text-xs text-center p-2">{favorite.name}</span>
                </div>
              )}
              <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center">
                <div className="h-12 w-12 rounded-full bg-primary flex items-center justify-center shadow-xl translate-y-4 group-hover:translate-y-0 transition-transform">
                  <Play className="h-6 w-6 text-primary-foreground fill-current" />
                </div>
              </div>
            </CardContent>
            <div className="p-3">
              <p className="font-medium text-sm truncate">{favorite.name}</p>
              <p className="text-xs text-muted-foreground truncate">{favorite.description || "Sonos Favorite"}</p>
            </div>
          </Card>
        ))}
      </div>
    </div>
  );
}
